
$quicksand: 'Quicksand', sans-serif;

$darkpurple:#1b0629;
$lightpurple: #dc93f8;
$purplegray: #43384f;

body {
  font-family: $quicksand;
  line-height: 1.5;
  box-sizing: border-box;
  *, *::before, *::after { box-sizing: inherit; }
}

img { max-width: 100%;}

.App {

  .container {
    padding: 1em;
    overflow-x: hidden;
  }

  .btn-switch {
    cursor: pointer;
    margin:0 0 1em 1em;
    -webkit-appearance: none;
    border: 1px solid #fff;
    box-shadow: 0 0 15px rgba(#000,.15);
    background: #fff;
    font-size: 11px;
    text-transform: uppercase;
    border-radius: 24px;
    width: 90px;
    height: 24px;
    position: relative;
    padding-left: 24px;
    color: #666;

    &:focus, &:hover {
      box-shadow: 0 0 15px rgba(#000,.25);
      outline: 0;
      color: $darkpurple;
    }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: -1px;
      width: 24px;
      height: 24px;
      border-radius: 100%;
      background-color: #ccc;
    }
  }

  &[data-theme='dark'] {
    background-color: $darkpurple;

    .btn-switch {
      padding-left: 0;
      padding-right: 20px;
      box-shadow: inset 1px 1px 5px #000;
      background-color: #000;
      color: $lightpurple;
      border-color:$lightpurple;
      &::before {
        left: calc(100% - 24px);
        background-color: $lightpurple;
      }
    }

    .project-card {
      border-color: rgba(#fff,.25);
      background-color: $darkpurple;
      .card-body * { color: #f4f4f4; }
      .card-image a div { opacity: .85; }
    }
    .back-link a {
      background-color: #000;
      color: $lightpurple;
      &:hover { color: #fff; text-shadow: none; }
    }
    .project-detail {
      .project-header *,
      .project-code * { color: #f4f4f4;}
      img, .site-link { border-color: rgba(#fff,.25);}
      .site-link:hover { background-color: rgba(#000,.5);}
    }
  }
  header {
    background-color: $darkpurple;
    padding: 1em;
    text-align: center;
      h1 { 
        font-weight: 300;
        margin: 5px;
        line-height: 1;
        font-size: 2em;
        letter-spacing: 1px;
        color: $lightpurple;
        text-shadow: 1px 1px 0 #fff;
        mix-blend-mode: color-dodge;
      }
      h2 { 
        font-weight: 300; 
        color: #fff; 
        margin: 1em 0 0;
        letter-spacing: 1px;
        font-size: 1.6em;
        font-weight: bold;
        opacity: .87;
      }
  
      h3 {
        margin: 0;
        color: $lightpurple;
        font-weight: normal;
        padding: 1em;
        line-height: 1.75;
        letter-spacing: 3px;
        font-size: .85em;
      }
  
      .contact {
        display: flex;
        max-width: 240px;
        margin: auto;
        align-items: center;
        justify-content: space-around;
        > a {
          display: block;
          margin: 5px auto;
          text-align: center;
          text-decoration: none;
          font-size: 20px;
          text-align: center;
          line-height: 45px;
          color: $lightpurple;
          width: 45px;
          height: 45px;
          border-radius: 50%;
          background: #000;
          box-shadow: 0 3px 0 rgba($lightpurple, .75);
          &:hover { color: #fff;  }
        }
      }
      
  }
  
 
  @media screen and (min-width: 768px) {
    display: grid;
    grid-template-columns: 320px 1fr;

    header {
      min-height: 100vh;
      padding: 2em 1em;
      h1 { font-size: 3em; }
      h2 { font-size: 2em; margin-top: 2em; }
      h3 { margin: 1em 0; padding: 2em; font-size: 1em; }
      .contact { position: sticky; top: 25px; }
  
    }

    // .code-compare {
    //   display: flex;
    //   pre ~ pre { margin-left: 10px; }
    // }
  }
}


.back-link {
  a {
    padding: .75em;
    display: inline-block;
    background-color: #dedede;
    text-decoration: none;
    &:hover { color: #000; text-shadow: 1px 1px 0 #fff;}
  }
}


@media (prefers-reduced-motion: no-preference) {
  .contact > a, .back-link a, a.site-link {
    transition: all .25s ease-in-out;
  }
  .contact > a, .back-link a {
    &:hover { transform: scale(1.05); }
  }
  .App, .btn-switch, .btn-switch::before {
    transition: all .2s ease-in-out;
  }
}


.project-card {
  display: flex;
  margin: 1em;
  border: 1px solid #fff;
  box-shadow: 0 0 35px rgba(#333,.15);
  align-items: center;

  .card-image {
    flex: 0 0 40%;
    height: 140px;
    a {
      display: block;
      position: relative;
      width: 100%;
      height: 100%;
      div {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        transition: all .5s ease-in;
        display: flex;
        align-items: center;
        justify-content: center;
        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background-color: rgba($darkpurple,.5);
            transition: opacity .33s ease-in;
            opacity: 0;
        }
        span {
            transition: opacity .33s ease-in;
            opacity: 0;
            color: #fff;
            text-decoration: none;
            z-index: 2;
        }
      }
      &:hover {
        div::before, span { opacity: 1; }
      }
    }
  }
  

  .card-body {
    padding: 1em;
    h4 {
      margin: 0 0 5px;
      font-weight: bold;
      a {
        text-decoration: none;
        color: $darkpurple;
        border-bottom: 1px solid transparent;
        transition: all .25s ease-in-out;
        &:hover { border-bottom-color: $darkpurple;}
      }
    }
    h5 { 
      margin: 0;
      color: #666;
      font-weight: 400;
      letter-spacing: 1px;
    }
  }
}

.project-detail {
  overflow-x: hidden;
  margin-top: 1em;

  pre, code { font-size: 14px !important; }

  img {
    border: 2px solid #fff;
    box-shadow: 0 0 35px rgba(#333,.15);
    width: 680px;
  }

  a.site-link {
    display: block;
    max-width: 680px;
    padding: 1em;
    background-color: rgba(#000,.15);
    text-align: center;
    border: 1px solid #fff;
    text-decoration: none;
    box-shadow: 0 0 35px rgba(#333,.15);
    &:hover { background-color: rgba(#000,.05);}
  }
}